  border-radius: 0.5rem;
  font-weight: 500;
  transition: all 0.2s;
  border: none;
  cursor: pointer;
}
.btn-danger:hover {
  background-color: #b91c1c;
}
.btn-danger:active {
  background-color: #991b1b;
}
.btn-danger:focus {
  outline: 2px solid #ef4444;
  outline-offset: 2px;
}
.btn-success {
  background-color: #16a34a;
  color: white;
  padding: 0.625rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 500;
  transition: all 0.2s;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  border: none;
