.input-field {
  width: 100%;
  padding: 0.625rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  transition: all 0.2s;
  outline: none;
}
.input-field::placeholder {
  color: #9ca3af;
}
.input-field:hover {
  border-color: #9ca3af;
}
.input-field:focus {
  border-color: transparent;
  box-shadow: 0 0 0 2px #3b82f6;
}
.select-field {
  width: 100%;
  padding: 0.625rem 1rem;
  border: 1px solid #d1d5db;
