  line-height: 1.25rem;
  font-weight: 600;
  border-radius: 9999px;
  background-color: #dbeafe;
  color: #1e40af;
}
.badge-danger {
  padding: 0.25rem 0.75rem;
  display: inline-flex;
  font-size: 0.75rem;
  line-height: 1.25rem;
